<template>
  <div class="menu-galery">
    <h2>PROTAGONISTAS</h2>
    <div v-for="item in items" :key="item.id" class="items">
      <div
        :class="item.id == Estado ? 'it active' : 'it'"
        :id="item.id"
        @click="CambioAct(item.id)"
      >
        {{ item.nombre }}
      </div>
    </div>
  </div>
</template>

<script>
import store from "@/store/index.js"
import dataArtistas from "@/artistas.json"
export default {
  name: "menugalery",

  props: {},
  data() {
    return {
      items: dataArtistas,
      Estado: store.state.activo,
      imagen: dataArtistas[store.state.activo].url,
      activoPers: store.state.activo,
    }
  },
  methods: {
    CambioAct: function(act) {
      store.commit("ActualizarStado", act)
      this.Estado = store.state.activo
      console.log("variable =", this.Estado)
      this.imagen = dataArtistas[this.activoPers].url
    },
  },
}
</script>

